<template>
  <div style="max-width: 100%;box-sizing: border-box;" class="feedlist">
    <myHeader
      :isBack="true"
      backPath="/business"
      title="死亡申报"
      subtitle="记录"
      subtitlePath="/deadApplicationRecord">
    </myHeader>

    <div class="mt4 list-block">
      <mt-cell title="批次号：" @click.native="selectBatchNum">
        {{batchNum}}
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="死亡日期：" @click.native="selectDeadDate">
        <span class="color-text">{{deadDateText}}</span>
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="死亡日龄：">{{deadDay}}天</mt-cell>
      <mt-cell title="申报死亡头数：">
        <input
          type="text"
          placeholder="请输入死亡头数"
          v-model="deedNumber"
          @input="handlerDeedNumberLimit"
        >头
      </mt-cell>

      <mt-cell title="死亡均重：">
        <input
          type="text"
          placeholder="请输入死亡均重"
          v-model="deepWeight"
          @input="handlerDeepWeightLimit"
        >kg
      </mt-cell>
      <div class="longitude mint-cell">
        <div class="width50 left">
          <span>经度：</span>
          <span>{{longitude}}</span>
        </div>
        <div class="width50 left">
          <span>纬度：</span>
          <span>{{dimension}}</span>
        </div>
      </div>
      <div class="photo-info">
        <div class="devied"></div>
        <mt-cell title="死亡猪只拍照："></mt-cell>
        <div class="photo-body">
          <div class="photoItem" v-for="(item,index) in photoList">
            <img :src="item.src" class="imgStyle">
            <span class="delete-photo" @click="deletePhoto(index)">
              <img src="@/assets/icon-delete.png">
            </span>
          </div>
          <div class="tabke-photo" @click="addPhoto">
            <img src="@/assets/icon-add.png" alt>
          </div>
        </div>
      </div>

      <SubmitBtn @click.native="submitPage"
                 :btnText="submitBtnText"
                 :customStyle="myCustomStyle"></SubmitBtn>
    </div>

    <div class="my-prop">
      <!--选择批次-->
      <mt-popup v-model="switchBatchNumPopup" position="bottom">
        <mt-picker :slots="batchNumSlots" value-key="name" :show-toolbar="true" ref="batchPicker">
          <span @click="handleBatchConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
    </div>

    <!--选择死亡日期-->
    <mt-datetime-picker ref="deadPicker" type="date" v-model="deadDate" @confirm="deadDateConfirm"></mt-datetime-picker>

    <!--死亡猪照片-->
    <!--<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>-->
  </div>
</template>
<script>
  import wx from "weixin-js-sdk";
  import myHeader from "@/components/Header";
  import SubmitBtn from "@/components/SubmitBtn";
  import {inputLimitNum, submitValidator} from "@/utils/validator";
  import {formatDate, toTransformTime} from "@/utils/tool";
  import {getOSType} from "@/utils/wxUtil";
  import {MessageBox} from "mint-ui";
  import {saveDeathApply,getDieDay,getBatchNumberList} from "@/api/business";
  import {mapGetters} from "vuex";
  import {Indicator} from 'mint-ui'
  import subMitBtnMixins from '@/mixins/subMitBtnMixins'

  export default {
    name: "deepApplication",
    mixins: [subMitBtnMixins],
    components: {
      SubmitBtn,
      myHeader
    },
    data() {
      return {
        batchNum: "",
        batchId: "",
        switchBatchNumPopup: false,
        batchNumSlots: [
          {
            values: [
              {
                id: "",
                name: "",
                birthDay: ""
              }
            ],
            textAlign: "center"
          }
        ],
        deadDate: new Date(),
        deadDateText: formatDate(new Date(), "yyyy-MM-dd"),
        deadDay: "",
        dieDay:"",
        birthDay: "",
        deedNumber: "",
        deepWeight: "",
        actualDeedNumber: "",
        longitude: "",
        dimension: "",
        sheetVisible: false,
        actions: [],
        currentPhotoInfo: {},
        showBigImage: false,
        bigImageSrc: "",
        photoList: [],
        serverId:[],
      };
    },
    created() {
      this._getBatchNumberList();
    },
    mounted() {
      this.initWxConfig();
      this.getLocation();
    },
    methods: {
      _getBatchNumberList() {
       Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        //获取批次列表
        getBatchNumberList().then(
          res => {
            Indicator.close();
           this.batchNumSlots[0].values = res.data.map((item, index) => {
              return {
                name: item.name,
                id: item.id,
                birthDay: item.birthday
              };
            });
           console.log('test',this.batchNumSlots[0].values);
            this.batchNum = this.batchNumSlots[0].values[0].name;
            this.batchId = this.batchNumSlots[0].values[0].id;
            this.birthDay = this.batchNumSlots[0].values[0].birthday;
            getDieDay(res.data[0].birthday,this.deadDateText).then((res)=>{
              this.deadDay = res.data;
            }).catch((err)=>{

            });
          },
          err => {
            Indicator.close();
          }
        );
      },
      selectBatchNum() {
        //选择批次
        console.log(this.$refs.batchPicker.getValues())
        if (this.batchNumSlots[0].values[0].name) {
          this.switchBatchNumPopup = !this.switchBatchNumPopup;
        } else {
          this.$toast({
            message: "无可选批次，请核实业务场景！",
            duration: 1000
          });
        }
      },
      handleBatchConfirm() {
        this.batchNum = this.$refs.batchPicker.getValues()[0].name;
        this.batchId = this.$refs.batchPicker.getValues()[0].id;
        this.birthDay = this.$refs.batchPicker.getValues()[0].birthDay;
        console.log(this.$refs.batchPicker.getValues());
        getDieDay(this.birthDay,this.deadDateText).then((res)=>{
          this.deadDay = res.data;
        }).catch((err)=>{

        });
        this.switchBatchNumPopup = false;
      },
      selectDeadDate() {
        //选择日期
        this.$refs.deadPicker.open();
      },
      deadDateConfirm(date) {
        //监听日期
        console.log(this.deadDate);
        this.deadDateText = formatDate(this.deadDate, "yyyy-MM-dd");
        getDieDay(this.birthDay,this.deadDateText).then((res)=>{
          this.deadDay = res.data;
        }).catch((err)=>{

        });
      },
      handlerDeedNumberLimit(e) {
        //限制死亡头数字段
        this.deedNumber = inputLimitNum(e);
      },
      handlerDeepWeightLimit(e) {
        //限制死亡重量字段
        this.deepWeight = inputLimitNum(e, 1);

      },
      getBase64ByLocalId(localId) {
        const v = this;
        wx.getLocalImgData({
          localId: localId.toString(), // 图片的localID
          success: res => {
            v.currentPhotoInfo.src = res.localData;
            v.uploadImg()
          }
        });
      },
      addPhoto() {
        if(this.photoList.length >=4){
          this.$toast('照片不能多于四张！');
          return false
        }
        this.takePhoto();
      },
      takePhoto: function () {
        let v = this;
        wx.ready(function () {
          wx.chooseImage({
            count: 1, // 默认9
            sizeType: ["original", "compressed"], // 可以指定是原图还是压缩图，默认二者都有
            sourceType: ["camera", 'album'], // 可以指定来源是相册还是相机，默认二者都有
            success: function (res) {
              const localId = res.localIds;
              v.currentPhotoInfo.loadId = localId;
              if (getOSType() === 1) {
                v.currentPhotoInfo.src = localId;
                v.uploadImg()
              } else if (getOSType() === 2) {
                v.getBase64ByLocalId(localId);
              }
            }
          });
        });
      },
      uploadImg() {
        let v = this;
        wx.uploadImage({
          localId: this.currentPhotoInfo.loadId.toString(), // 需要上传的图片的本地ID，由chooseImage接口获得
          isShowProgressTips: 1, // 默认为1，显示进度提示
          success: function (res) {
            // v.$toast(res.serverId);
            const photo = v.currentPhotoInfo;
            photo.serviceId = res.serverId;
            v.currentPhotoInfo = {};
            v.photoList.push(photo);
            v.serverId.push(res.serverId);
            v.$toast({
              message:'上传成功!',
              duration:2000
            });
        /*    uploadMsg(res.serverId) //向后台发送serverId参数
              .then(response => {
                // v.$toast(response.toString())
              })
              .catch(error => {
                // v.$toast(error.toString())
              });*/
          },
          fail: function (res) {
            this.$toast("上传失败:" + JSON.stringify(res));
            v.currentPhotoInfo = {};
          }
        });
      },
      deletePhoto(index) {
        let _this = this;
        MessageBox.confirm("确定执行此操作?").then(action => {
          this.photoList.splice(index, 1);
          this.serverId.splice(index,1);
        });
      },
      _checkData() {
        const checkList = [
          {
            val: this.batchNum,
            rule: ["required"],
            getTipText: "批次号"
          },
          {
            val: this.deedNumber,
            rule: ["required"],
            getTipText: "死亡头数"
          },
          {
            val: this.deepWeight,
            rule: ["required"],
            getTipText: "死亡均重"
          }
        ];
        let flag = submitValidator(checkList);
        return flag;
      },
      _submitData() {
        const submitData = {
          batchId: this.batchId,
          batchNo: this.batchNum,
          dieDate: this.deadDateText,
          dieDay: this.deadDay,
          applyDieCnt: this.deedNumber,
          dieAvg: this.deepWeight,
          latitude: this.dimension, //维度
          longitude: this.longitude, //经度,
          serverIds:this.serverId
        };
        saveDeathApply(submitData).then(
          res => {
            this.$toast({
              message: "提交成功！",
              duration: 1000
            });
            this.$router.push("/deadApplicationRecord");
          }
        ).catch(err => {
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        });
      },
      submitPage() {
        let flag = this._checkData();
        if (!flag) return false;

        if (this.photoList.length < 2) {
          this.$toast('至少2张多角度照片！');
          return false
        }else if(this.photoList.length >4){
          this.$toast('照片不能多于四张！');
          return false
        }

        if (!this.isDisabledSubmit) {
          this.isDisabledSubmit = true;
          this.submitBtnText = '提交中...';
          this._submitData();
        }

      },
      initWxConfig() {
        wx.config({
          debug: false, //
          appId: this.signature.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
          timestamp: this.signature.timestamp, // 必填，生成签名的时间戳
          nonceStr: this.signature.nonceStr, // 必填，生成签名的随机串
          signature: this.signature.signature, // 必填，签名，见附录1
          jsApiList: [
            "chooseImage",
            "getLocation",
            "getNetworkType",
            "uploadImage",
            "previewImage"
          ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        });
      },
      getLocation() {
        let v = this;
        wx.ready(function () {
          wx.getLocation({
            type: "wgs84", // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
            success: function (res) {
              v.dimension = Math.floor(res.latitude * 10000000) / 10000000; // 纬度，浮点数，范围为90 ~ -90
              v.longitude = Math.floor(res.longitude * 10000000) / 10000000; // 经度，浮点数，范围为180 ~ -180。
            }
          });
        });
      }
    },
    computed: {
      ...mapGetters(["signature"])
    }
  };
</script>
<style lang="scss" scoped>
  .feedlist {
    min-height: 100vh;
    background-color: #f8f8f8;
    padding: 0.12rem 0;

    .list-block {
      font-size: 0.16rem;
      background-color: #fff;

      .deepWeight {
        display: block;
        margin-left: 0.06rem;
      }

      .img {
        width: 0.1rem;
        height: 0.06rem;
        margin-left: 0.08rem;
        background: url("~@/assets/icon-next.png") no-repeat;
        background-size: 100%;
      }

      input {
        font-size: 0.16rem;
        text-align: right;
      }
    }

    .longitude {
      display: flex;
      overflow: hidden;
      position: relative;
      line-height: 1;
      padding: 0 0.1rem;

      .width50 {
        width: 50%;

        span {
          display: flex;
          float: left;
          flex: 1;
          color: #000;
          min-height: 0.44rem;
          align-items: center;
          word-break: break-all;
          justify-content: flex-start;
          vertical-align: middle;
        }
      }

      .left {
        float: left;
      }
    }

    .photo-info {
      .devied {
        height: 0.1rem;
        background: #f8f8f8;
      }

      background: #fff;

      .mint-cell {
        border-bottom: none;
      }

      .photo-body {
        margin-top: 0.25rem;
        padding: 0 0.1rem 0.25rem;

        .photoItem,
        .tabke-photo {
          width: 0.9rem;
          height: 0.9rem;
          display: inline-flex;
          flex-wrap: wrap;
          justify-content: flex-start;
          border: 1px dashed #868282;
          margin-right: 0.16rem;
          position: relative;

          .imgStyle {
            width: 0.88rem;
            height: 0.88rem;
          }

          .delete-photo {
            position: absolute;
            top: -0.1rem;
            right: -0.1rem;
            width: 0.2rem;
            height: 0.2rem;
          }
        }

        .tabke-photo {
          img {
            width: 0.88rem;
            height: 0.88rem;
          }
        }
      }
    }

    .my-prop {
      .mint-popup {
        width: 100%;
        height: 25%;
      }

      /deep/ .picker-toolbar {
        height: 0;
      }

      .sure-btn {
        position: absolute;
        top: 0.05rem;
        right: 0.05rem;
        padding: 0.05rem 0.2rem;
        font-size: 0.16rem;
        z-index: 99;
      }
    }
  }
</style>



